import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {
  user;
  baseURL = "http://" + environment.apiBaseUrl;
  delKey;
  data = [];
  key;
  srcCol = ['name', 'customer', 'purchased', 'total', 'order_at', 'status'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];//set user information
    this.getItem();//get order information from service
  }
  //CRUD functions related to Service
  deliver(id) {//order deliver
    const editkey = this.findIndex(id, this.data);
    this.data[editkey].status = "Delivered";
    this.userService.editOrder(this.data[editkey]).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  paid(id) {//order paid
    const editkey = this.findIndex(id, this.data);
    this.data[editkey].status = "Paid";
    this.userService.editOrder(this.data[editkey]).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  deleteItem() {//Delete order information
    this.userService.delOrder(this.data[this.delKey]).subscribe(
      res => {
        this.handleSuccess();
      },
      err => {
        this.handleError(err);
      }
    );
  }
  getItem() {//Get order information
    this.userService.getOrder(this.user).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
  del(id) {//function when "delete" button click
    this.delKey = this.findIndex(id, this.data);
  }
  findIndex(id, datas) {//function to find index of table data
    for (let i = 0; i < datas.length; i++) {
      if (datas[i]._id === id) return i;
    }
    return 0;
  }
  getDateString(day){//convert date
    return new Date(day).toISOString();
  }
  getPurchase(item){
   if(item.length==0) return "No item"
   else if(item.length==1) return item[0][0];
   else return (''+item.length+' items')
  }
  applyFilter(arr) {//search function of table
    if (!this.key) return true;
    for (let i = 0; i < this.srcCol.length; i++) {
      if (String(arr[this.srcCol[i]]).search(this.key) != -1) {
        return true;
      }
    }
    return false;
  }

  downloadCSV() {
    if (this.data.length == 0) return;
    const csvOption = {
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalseparator: '.',
      showLabels: true,
      showTitle: false,
      useBom: true,
      noDownload: false,
      headers: Object.keys(this.data[0])
    };
    console.log(Object.keys(this.data[0]))
    new AngularCsv(this.data, "Orderlist", csvOption);
  }
  uploadCSV() {
    document.getElementById('csv').click();

  }
  importCSV(event) {
    const file = event.target.files[0];
    let fileReader: FileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = ev => {
      let csvdata = fileReader.result.toString();
      let body = { data: csvdata };
      this.userService.importCsvOrder(body).subscribe(
        res => {
          this.handleSuccess()
        },
        err => {
          this.handleError(err)
        }
      );
    };
    event.target.value = "";
  }
  //Toastr functions
  handleError(err) {
    if (err.status == 504) this.showSuccessToaster('error', '', 'server is not responsing.');
    else this.showSuccessToaster('error', '', err.error.message);
  }
  handleSuccess() {
    this.showSuccessToaster('success', '', 'success')
    this.getItem();
  }
  showSuccessToaster(style, msgtopic, msgContent) {
    this.toastr.show(style, msgtopic, msgContent);
  }
}
