import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit} from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  user;
  baseURL = "http://" + environment.apiBaseUrl;
  //for formInput
  category = [];
  formGroup: FormGroup;
  //for carousel image display
  fileChange = false;
  imageList = [];
  activeImage = 0;
  carouselButtonDisplay = false;
  //for table
  key;
  editStatus = false;
  addStatus = false;
  delKey;
  editKey;
  data = [];
  srcCol=['title','category','regularPrice','salePrice','quantity','sku','status'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  
  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];//set user information
    this.setCategory();//set category select list
    this.refresh();//set all data
    this.getItem();//get customer information from service
  }
   //CRUD functions related to Service
   addItem() {//Create new Product
    this.userService.createProduct(this.formGroup.value).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  editItem() {//Update Product information
    this.userService.updateProduct(this.formGroup.value).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  deleteItem() {//Delete Product information
    this.userService.delProduct(this.data[this.delKey]).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
       this.handleError(err)
      }
    );
  }
  getItem() {//Get Product information
    this.userService.getProduct(this.user).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
  setCategory(){//set for category select list
    this.userService.getCategory(this.user).subscribe(
      res=>{
        this.category=res['result'];
      }
    );
  }
  setImageList(imgArray){//set carousel image list
      if(imgArray.length==0) this.imageList=['assets/img/costic/add-product-1.jpg'];
      else this.imageList=imgArray;
  }
 //functions related to table component
 add() {//function when "add" button click
 window.scroll(0,0)
 this.addStatus = true;
 }
 edit(id) {//function when "edit" button click
  window.scroll(0,0)
   this.editKey = this.findIndex(id,this.data);
   this.editStatus = true;
   let insertValue= this.data[this.editKey];
   delete insertValue.__v;
   this.formGroup.setValue(insertValue);
   this.setImageList(this.formGroup.value.image);
 }
 del(id) {//function when "delete" button click
   this.activeImage=0;
   this.delKey = this.findIndex(id,this.data);
 }
 findIndex(id,datas) {//function to find index of table data
  for(let i=0;i<datas.length;i++){
    if(datas[i]._id===id) return i;
  }
  return 0;
 }
 applyFilter(arr){//search function of table
   if(!this.key) return true;
   for(let i=0;i<this.srcCol.length;i++){
     if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
       return true;
     }
   }
   return false;
 }
 downloadCSV(){
  if(this.data.length==0) return;
  const csvOption={
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: true,
    showTitle: false,
    useBom: true,
    noDownload: false,
    headers: Object.keys(this.data[0])
  };
  new  AngularCsv(this.data, "Productlist", csvOption);
}
uploadCSV(){
  document.getElementById('csv').click();
}
importCSV(event){
  const file = event.target.files[0];
    let fileReader: FileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = ev => {
      let csvdata = fileReader.result.toString();
      let body = {data:csvdata};
      this.userService.importCsvProduct(body).subscribe(
        res => {
         this.handleSuccess()
        },
        err => {
         this.handleError(err)
        }
      );
    };
  event.target.value="";
}
//add and remove carousel
addCarousel(){
  document.getElementById('photo').click();
}
delCarousel(){
 this.imageList.splice(this.activeImage,1);
}
 //File manage function
 selectFile(event) {//function when file input "ok" button click. Here new carousel image is inserted.
 let reader = new FileReader();
 this.fileChange = true;
 const mimeType = event.target.files[0].type;
 const file = event.target.files[0];
 reader.readAsDataURL(file);
 reader.onload = (_event) => {
   if(this.imageList.length>0&&this.imageList[0]=='assets/img/costic/add-product-1.jpg') this.imageList=[];
   this.imageList=this.putInMiddle(this.imageList,this.activeImage,reader.result)
 };
 event.target.value="";
}
//mouse events on carousel div
 mouseLeave(){
   this.carouselButtonDisplay=false;
}
 mouseenter(){
  this.carouselButtonDisplay=true;
}
 //functions related to Information Modal component
 save() {//function when "save" button of Modal click
   if(!this.formGroup.valid){
     this.showSuccessToaster('error','','Please provide content correctly');
     return
   }
   this.formGroup.value.image=this.imageList;
   if (this.addStatus == true) this.addItem();
   else if (this.editStatus == true) this.editItem();
 }
 cancel() {//function when "cancel" button of Modal click
   this.refresh();
 }
 getSource(item){//get product image in table
  if(item.length>0)  return item[0]
  else return 'assets/img/costic/add-product-1.jpg';
 }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.fileChange = false;
    this.activeImage = 0;
    this.formGroup = this._formBuilder.group({
      _id: [''],
      title: ['title of product', Validators.required],
      category: ['',Validators.required],
      regularPrice: [0, Validators.required],
      salePrice: [0, Validators.required],
      quantity: [0, Validators.required],
      sku: ['234dert', Validators.required],
      currency: ['USD', Validators.required],
      detail: [''],
      image:[[]],
      video:[''],
      created_at: [new Date()],
      saled:[0],
      status: ['in stock', Validators.required],
    });
    this.setImageList([]);
  }
  //Toastr functions
  handleError(err){
    if(err.status==504)  this.showSuccessToaster('error', '', 'server is not responsing.');
    else         this.showSuccessToaster('error', '', err.error.message);
  }
  handleSuccess(){
    this.showSuccessToaster('success', '', 'success')
    this.getItem();
    this.refresh();
  }
   showSuccessToaster(style, msgtopic, msgContent) {
     this.toastr.show(style, msgtopic, msgContent);
   }
   putInMiddle(arr,key,member){//insert memeber to in the middle of array
    const temp=[];
    for(let i=0;i<key;i++) temp.push(arr[i])
    temp.push(member);
    for(let i=key;i<arr.length;i++) temp.push(arr[i])
    return temp;
  }
 }