import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {
  user;
  baseURL = "http://" + environment.apiBaseUrl;
  modalStatus = false;
  editStatus = false;
  addStatus = false;
  delKey;
  editKey;
  data = [];
  formGroup: FormGroup;
  reader = new FileReader();
  formData = new FormData();
  fileChange = false;
  fileDish;
  fileURL;
  fileName;
  key;
  srcCol=['name','created_by'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  
  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];//set user information
    this.refresh();//set all data
    this.getItem();//get Category information from service
  }
  //CRUD functions related to Service
  addItem() {//Create new Category
    this.userService.createCategory(this.formGroup.value).subscribe(
      res => {
        this.uploadAvatar();
        this.showSuccessToaster('success', '', 'success');
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
        this.refresh();
      }
    );
  }
  editItem() {//Update Category information
    this.userService.updateCategory(this.formGroup.value).subscribe(
      res => {
        this.uploadAvatar();
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
        this.refresh();
      }
    );
  }
  deleteItem() {//Delete Category information
    this.userService.delCategory(this.data[this.delKey]).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
       this.handleError(err)
        this.refresh();
      }
    );
  }
  getItem() {//Get Category information
    this.userService.getCategory(this.user).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
  uploadAvatar(){//Upload img. Not used in this component.
    if(!this.fileChange) return true;
    this.formData.append('file', this.fileDish, this.fileName);
    this.userService.uploadAvatar(this.formData).subscribe(
      res=>{
        this.getItem();
      },
      err=>{
      }
    );
 }
   //functions related to table component
   add() {//function when "add" button click
   this.addStatus = true;
 }
 edit(id) {//function when "edit" button click
   this.editKey = this.findIndex(id,this.data);
   this.editStatus = true;
   let insertValue= this.data[this.editKey];
   delete insertValue.__v;
   this.formGroup.setValue(insertValue);
 }
 del(id) {//function when "delete" button click
   this.delKey = this.findIndex(id,this.data);
 }
 findIndex(id,datas) {//function to find index of table data
  for(let i=0;i<datas.length;i++){
    if(datas[i]._id===id) return i;
  }
  return 0;
 }
 applyFilter(arr){//search function of table
   if(!this.key) return true;
   for(let i=0;i<this.srcCol.length;i++){
     if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
       return true;
     }
   }
   return false;
 }
 downloadCSV(){
  if(this.data.length==0) return;
  const csvOption={
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: true,
    showTitle: false,
    useBom: true,
    noDownload: false,
    headers: Object.keys(this.data[0])
  };
  console.log(Object.keys(this.data[0]))
  new  AngularCsv(this.data, "Categorylist", csvOption);
}
uploadCSV(){
  document.getElementById('csv').click();

}
importCSV(event){
  const file = event.target.files[0];
    let fileReader: FileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = ev => {
      let csvdata = fileReader.result.toString();
      let body = {data:csvdata};
      this.userService.importCsvCategory(body).subscribe(
        res => {
          this.showSuccessToaster('success', '', 'success')
          this.refresh();
          this.getItem();
        },
        err => {
         this.handleError(err)
          this.refresh();
        }
      );
    };
  event.target.value="";
}
 //functions related to Information Modal component
 save() {//function when "save" button of Modal click
   if(this.fileChange==true){
     const ns = this.formGroup.value.username;
     this.fileName = "uploads/avatar/" + ns + String(Date.now())+ '.' + this.fileDish.name.split('.')[this.fileDish.name.split('.').length - 1];
     this.formGroup.value.avatar=this.fileName;
   }
   if (this.addStatus == true) this.addItem();
   else if (this.editStatus == true) this.editItem();
 }
 cancel() {//function when "cancel" button of Modal click
   this.refresh();
 }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.fileDish = undefined;
    this.fileURL = undefined;
    this.fileChange = false;
    const useravatar='uploads/avatar/sample-'+Math.floor(Math.random()*9)+'.jpg';
    this.formData = new FormData();
    const intenationalPhone = /\+(9[976]\d|8[987530]\d|6[987]\d|5[90]\d|42\d|3[875]\d|2[98654321]\d|9[8543210]|8[6421]|6[6543210]|5[87654321]|4[987654310]|3[9643210]|2[70]|7|1)\d{1,14}$/;
    this.formGroup = this._formBuilder.group({
      _id:[''],
      name: ['', Validators.required],
      created_by: [this.user.username],
    });
  }
   //Toastr functions
   handleError(err){
    if(err.status==504)  this.showSuccessToaster('error', '', 'server is not responsing.');
    else         this.showSuccessToaster('error', '', err.error.message);
   }
   showSuccessToaster(style, msgtopic, msgContent) {
     this.toastr.show(style, msgtopic, msgContent);
   }
   //File manage function
   selectFile(event) {//function when file input "ok" button click
     this.fileChange = true;
     const mimeType = event.target.files[0].type;
     this.fileDish = event.target.files[0];
     this.reader.readAsDataURL(event.target.files[0]);
     this.reader.onload = (_event) => {
       this.fileURL = this.reader.result;
     };
     event.target.value="";
   }
   fileButtonClick() {//function when Avatar image click
    if(this.addStatus)  document.getElementById('photo').click();
   }
   getAvatarSource() {//function for image source
     if (this.fileChange) return this.fileURL;
     else return this.baseURL + this.formGroup.value.avatar;
   }
 }
 