import { UserService } from './../../../shared/userService/user.service';
import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../shared/toastrService/toaster.service';
@Component({
  selector: 'app-default-login',
  templateUrl: './default-login.component.html',
  styleUrls: ['./default-login.component.css']
})
export class DefaultLoginComponent implements OnInit {
  formGroup: FormGroup;
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    this.formGroup = this._formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
		});
  }
  login(){
    if(!this.formGroup.invalid){
      this.userService.login(this.formGroup.value).subscribe(
        res=>{
          this.userService.setToken(
            {
              token:res['token'],
              userInfo:res['userInfo'],
              expiresAt:res['expiresAt']
            }
          );
          this.showSuccessToaster('success','','Logined successfully!'); 
          this.router.navigateByUrl('/');
        },
        err=>{
          if(err.status==504) this.showSuccessToaster('error','','server is not responsing') 
          else this.showSuccessToaster('error','',err.error.message) 
        }
      )
    }
  }
  showSuccessToaster(style,msgtopic,msgContent) {
    this.toastr.show(style, msgtopic, msgContent);
}
}
